import request from "../../utils/request.js"
import tabar from '../../components/tabBar/tabBar.vue'
import login from '../../utils/login.js'
export default {
	components: {
		tabar
	},
	data() {
		return {
			shade: false,
			tab: 1,
			SessionKey: '',
			OpenId: '',
			//nickName: null,
			avatarUrl: null,
			gender: null,
			isCanUse: uni.getStorageSync('isCanUse') || true, //默认为true，
			user: uni.getStorageSync('user'),
			worksList: {
				page: 1,
				list: [],
				pageLength:0
			},
			collectionList: {
				page: 1,
				list: []
			}
		}
	},
	onLoad() {
		this.user = uni.getStorageSync('user');
		this.worksList.page = 1;
		this.works();
	},
	onReachBottom() {
		console.log("到底了")
		this.works()
	},
	methods: {
		// 切换选项（作品，收藏，喜欢）
		tabNav(e) {
			this.tab = e
		},
		redirectTo(e) {
			uni.navigateTo({
				url: e
			});
		},
		updaterProfile() {
			uni.navigateTo({
				url: "userinfo/update?code=3"
			});
		},
		works() {
			var page = this.worksList.page
			var _this = this;
			var id=uni.getStorageSync('user').id;
			request({
				url: '/index/getUservideo',
				data: {
					id:id,
					page: page
				},
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				success: (res) => {
					console.log(res)
					var list = _this.worksList.list;
					if (res.data) {
						var list2 = list.concat(res.data.data.list);
						_this.worksList.list = list2;
						_this.worksList.page = _this.worksList.page + 1;
						_this.worksList.pageLength=res.data.data.pageLength
					}
				}
			});
		}
	}
}
